.wrap {
  width: 100%;
  height: auto;
  height: 4000px;
}

#header {
  width: 100%;
  height: auto;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 20;
  color: #000;
  border-bottom: 1px solid #000;
  background-color: rgba(255, 255, 255, 0.7);
}

#header .inner {
  max-width: 1024px;
  margin: 0 auto;
  position: relative;
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  height: 50px;
}

#header .inner .logo {
  position: relative;
  line-height: 50px;
}

#header .inner .logo::after {
  display: block;
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  border-radius: 50%;
  background-color: #f7df1d;
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  position: absolute;
  top: 50%;
  left: 65%;
  font-size: 8px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

#header .inner .logo a {
  display: table-cell;
  color: #000;
  font-family: "Poppins", sans-serif;
  font-style: italic;
  font-weight: 600;
  position: relative;
  z-index: 4;
}

#header .inner .logo span {
  display: block;
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  z-index: -2;
  clip: rect(1px, 1px, 1px, 1px);
}

#header .inner .gnb {
  height: 50px;
}

#header .inner .gnb .menu_gnb {
  display: flex;
  justify-content: space-between;
  line-height: 50px;
  width: 500px;
}

#header .inner .gnb .menu_gnb li {
  position: relative;
}

#header .inner .gnb .menu_gnb li::after {
  display: block;
  position: absolute;
  content: "";
  width: 0%;
  height: 2px;
  top: 70%;
  left: 0;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  background-color: #000;
  -webkit-transition: 0.4s ease-in-out;
  transition: 0.4s ease-in-out;
}

#header .inner .gnb .menu_gnb li:hover::after {
  width: 100%;
}

#header .inner .gnb .menu_gnb a {
  font-family: "Poppins", sans-serif;
  font-style: italic;
  font-weight: 600;
  color: #000;
  text-transform: uppercase;
  font-size: 14px;
}

@media screen and (max-width: 960px) {
  #header .inner .gnb .menu_gnb {
    display: none;
  }
}

